import React from 'react';
import { FormGroup } from '@material-ui/core';

import { $activity } from '../../../../declarations/local/activity';
import { $materialRowProps } from '..';
import MaterialRow from '../Row';
import Materials from '../Materials';


export default function MaterialsList({activity}: {activity: $activity}){
  return <Materials activities={[activity]}
    render={
      (materialRowProps: $materialRowProps[])=><FormGroup>
        {
          materialRowProps.map((rowProps: $materialRowProps) => <MaterialRow key={rowProps.id + rowProps.metaRequirement.raw} {...rowProps} />)
        }
      </FormGroup>
    }
  />
}