import React, { useEffect, useState } from 'react';
import { Grid, Typography } from '@material-ui/core';
import SheduleRow from '../../../../components/general/SheduleRow';
import { Link, useHistory } from 'react-router-dom';
import FAIcon from '../../../../components/general/FAIcon';
import { useSelector, useDispatch } from 'react-redux';




import { purgeActivities } from '../utils';


import { uiSelector, createUIAction } from '../../../../../utils/redux/ui';
import { useMediaQuery } from '@material-ui/core';
import MyTabs, { TabPanel } from '../../../../components/general/Tabs';


import ImageSlotTour, {
    collectionSteps,
    mobileCollectionSteps
} from '../../../../components/general/SheduleRow/ImageSlot/Tour';



import addedActivitiesWrapper from '../wrapper';
import {
    onboardingSelector,
    fullOnboardingSelector,
    createConfigAction,
    addedActivitiesSelector
} from '../../../../../utils/redux/config';
import { removeActivity } from '../utils';
import { Provider } from '../../../../../utils/context/rowBlackbox';
import MasterList from '../../../../materials/MasterList';
import { $activity } from '../../../../../../declarations/local/activity';

const noActivities = <p>You have no bookmarked activities. To bookmark an activity, click on the <FAIcon type="bookmark"/> icon of an activity card on your <Link to="/">home page</Link></p>

const defaultValue : any = []

function ActivityCollections({data}: {data: $activity[]}){  
  const addedActivities = useSelector(addedActivitiesSelector);
  const dispatch = useDispatch();
  const ui = useSelector(uiSelector);
  const tab = ui.addedActivities.tab || 0;
  const onboarding = useSelector(fullOnboardingSelector);
  const [mounted, setMounted] = useState(false);

  useEffect(()=>{
    setMounted(true);
  }, [])
  React.useEffect(()=>{
    if(data !== defaultValue){
      if(data.length !== Object.keys(addedActivities).length){
        purgeActivities(data)
      }
    }
  // eslint-disable-next-line
  }, [data])
  const onboardingcollections = useSelector(onboardingSelector('collections'));
  const conditionCheck = (onboarding: any)=>onboarding?.dashboard?.completed;
  const bookmarkedActivities = !data.length ? noActivities : 
  <Provider value={{ids: {}, row: 0, imageType: 'portrait', doNotHideAddedActivities: true}}>
    <SheduleRow
      showFirstActivity={conditionCheck(onboarding) && !onboardingcollections.completed}
      data={data}
    />
  </Provider>;
  
  const biggerThanSmall = useMediaQuery('(min-width:1000px)');

  const tour = <ImageSlotTour noOnEnd type="collections" conditionCheck={conditionCheck} />
  if(biggerThanSmall){
    return <div>
      {
        onboarding?.dashboard?.completed && !onboarding?.collections?.completed && <OnboardingTripwire2 />
      }
      {
        mounted && Boolean(data.length) && tour
      }
      <Typography align="center" variant="h6">
        Bookmarks
      </Typography>
      <Grid container>
        <Grid xs={7}>
          {
            bookmarkedActivities
          }  
        </Grid>  
        <Grid xs={5}>
          <MasterList data={data} />
        </Grid>
      </Grid>
    </div>
  }
  return <div>
    {
      !onboarding?.collections?.completed && <><OnboardingTripwire /><OnboardingTripwire2 mobile /></>
    }
    <ImageSlotTour key={tab} noOnEnd type="collections" mobile conditionCheck={(onboarding: any)=>onboarding?.dashboard?.completed}/>
      <div>
        <MyTabs
          tabsProps={{variant: "standard"}}
          value={tab}
          onChange={(newValue: number)=>{
            dispatch(createUIAction({
              addedActivities: {
                tab: newValue
              }
            }))
          }}
          tabPropsArr={
            [
              {
                label: "Bookmarks"
              },
              {
                label: "Master Supplies List",
                classes: {
                  root: 'supplies-tab'
                }
              },
            ]
          } />
      </div>
      <TabPanel value={tab} index={0}>
        {
          bookmarkedActivities
        }
      </TabPanel>
      <TabPanel value={tab} index={1}>
        {
          <MasterList data={data} />
        }
      </TabPanel>
    </div>
}

const OnboardingTripwire = React.memo(function OnboardingTripwire(){
  const dispatch = useDispatch();
  const onboarding = useSelector(fullOnboardingSelector);
  const ui = useSelector(uiSelector);
  useEffect(()=>{
    if(onboarding?.dashboard?.completed && !onboarding.collections?.completed && onboarding.collections?.step === 0){
      dispatch(createUIAction({
        addedActivities: {
          ...ui.addedActivities,
          tab: 0,
        }
      }));
    }
    // eslint-disable-next-line
  }, [])
  useEffect(()=>{
    if(onboarding?.dashboard?.completed && !onboarding.collections?.completed && onboarding.collections.step === 3){
      dispatch(createUIAction({
        addedActivities: {
          ...ui.addedActivities,
          tab: 1,
        }
      }));
    }
    // eslint-disable-next-line
  }, [onboarding.collections.step])
  return null;
})

const OnboardingTripwire2 = React.memo(function OnboardingTripwire2(props: {mobile?: Boolean}){
  const dispatch = useDispatch();
  const history = useHistory();
  const onboarding = useSelector(fullOnboardingSelector);
  const addedActivities = useSelector(addedActivitiesSelector);
  const steps = props.mobile ? mobileCollectionSteps : collectionSteps;
  useEffect(()=>{
    if(onboarding?.collections?.step === steps.length && !onboarding?.collections?.completed){
      dispatch(createConfigAction({
        onboarding: {
          ...onboarding,
          collections: {
            ...onboarding.collections,
            completed: true,
            step: onboarding.collections.step + 1
          }
        }
      }));
      if(Object.keys(addedActivities).length === 3){
        Object.keys(addedActivities).forEach( id =>{
          dispatch(removeActivity({id}))
        })
      }
      history.push('/');
    }
  // eslint-disable-next-line
  }, [onboarding])
  return null;
})

export default addedActivitiesWrapper(ActivityCollections)