import React from 'react'
import classnames from 'classnames'

type $props = {outline?: Boolean, light?: Boolean, raw?: string, className?: string, size?: string|number, type?: string, style?: Object}

const FAIcon = React.forwardRef(function FAIcon(props: $props, ref: any){
  const type = getType(props);
  const name = getName(props);
  const size = props.size
  return <i ref={ref} style={props.style || {}} className={classnames(props.className, type, name, size && 'la-'+size)} />
});

export default FAIcon;

function getType(props: $props){
  if(props.outline) return 'lar';
  if(props.light) return 'lal';
  if(props.raw) return props.raw.split(' ')[0];
  return 'las';
}

function getName(props: $props){
  if(props.raw) return props.raw.split(' ')[1];
  return `la-${props.type}`
}