import React from 'react';
import { FormControlLabel } from '@material-ui/core';
import { $materialRowProps } from '..';
import { Checkbox } from '@material-ui/core';

const MaterialRow = React.memo(function MaterialRow({checked, checkedClass, onChange, metaRequirement}: $materialRowProps){
  return <FormControlLabel
      control={
        <Checkbox
          classes={{
            checked: checkedClass,
            root: 'checkbox-square'
          }}
          checked={checked}
          onChange={onChange} 
          name={metaRequirement.raw} />
        }
      label={
        metaRequirement.types.join(' or ')
      }
    />
})

export default MaterialRow;