import React, { useEffect, useState } from 'react';
import _ from 'lodash';
import LazyLoad from 'react-lazyload';

import SearchActivitiesNonSearchRow from '../NonSearchRow';
import SearchActivitiesNonSearchPortraitRow from '../NonSearchPortraitRow';
import SearchActivitiesNonSearchSquareRow from '../NonSearchSquareRow';
import lscache from 'lscache';
import { useCustomSwr, SERVER_ADDRESS } from '../../../../utils/remote';
import { Provider, $rowBlackboxContext } from '../../../../utils/context/rowBlackbox';
import PackRow from '../NonSearchPackRow';

const LIST_ACTIVITIES_TAG_ORDER = 'listActivitiesTagOrder';

const mainTags = [/*'active',*/ 'stem', 'arts&crafts', 'indoor', 'outdoor', 'literacy', 'math', 'printable', 'winter'];

export default React.memo(function ListActivities({ages}: {subjects: string[], ages: string[]}){
  const [tagOrder, setTagOrder] = useState<string[]>(JSON.parse(lscache.get(LIST_ACTIVITIES_TAG_ORDER) || '[]'));
  const [ids] = useState({});
  const { data: popularTags } : {data: string[]} = useCustomSwr(SERVER_ADDRESS+'/tags/popular', {
    cache: true,
    defaultValue: []
  });
  useEffect(()=>{
    if(!tagOrder.length && popularTags.length){
      const nextTagOrder = _.uniq((_.shuffle(mainTags)).concat(_.shuffle(popularTags||[]).filter((tag: string)=>(tag.indexOf('age') === -1))))
      setTagOrder(nextTagOrder);
      lscache.set(LIST_ACTIVITIES_TAG_ORDER, JSON.stringify(nextTagOrder), 15)
    }
  // eslint-disable-next-line
  }, [ages, popularTags])

  const squareRender = (tag: string)=>{
    return <div style={{minHeight: '300px', minWidth: '100%'}}><LazyLoad height={200} >
      <SearchActivitiesNonSearchSquareRow tag={tag} ages={ages} />
    </LazyLoad></div>;
  }

  const packRender = (tag: string)=>{
    return <div style={{minHeight: '300px', minWidth: '100%'}}><LazyLoad height={200} >
      <PackRow subjectTags={[tag].concat(tagOrder.reverse().slice(0, 5))} ages={ages} />
    </LazyLoad></div>;
  }

  const portraitRender = (tag: string)=>{
    return <div style={{minHeight: '300px', minWidth: '100%'}}><LazyLoad height={200} >
      <SearchActivitiesNonSearchPortraitRow tag={tag} ages={ages} />
    </LazyLoad></div>;
  }
  
  const render = (tag: string)=>{
    return <div style={{minHeight: '300px', minWidth: '100%'}}><LazyLoad height={300}>
      <SearchActivitiesNonSearchRow tag={tag} ages={ages} />
    </LazyLoad></div>;
  }

  const renderAll = (tag: string, row: number)=>{
    if(!tag) return null;
    if(row === 1) return packRender(tag);
    if(row === 8) return squareRender(tag)
    if(row === 3 || row === 6 || row === 12) return portraitRender(tag)
    return render(tag)
  }

  return <div>
    {
      // [tagOrder[0]]
      tagOrder
      .map((tag: string, row: number)=>{
        const context : $rowBlackboxContext = {
          ids,
          row,
          imageType: (row % 2 === 1) ? 'landscape' : 'portrait',
        };
        return <Provider key={tag} value={context}>{renderAll(tag, row)}</Provider>
      })
    }
  </div>
});