import React from 'react';
import { makeStyles } from '@material-ui/core';
import SheduleSlot from './Slot';
import PortraitSlot from './PortraitSlot';
import SquareSlot from './SquareSlot';
import Scroll from '../Scroll';
import ImageSlot from './ImageSlot';
import { useRowBlackbox } from '../../../../utils/context/rowBlackbox';

type $props = {
  data: any[],
  featuredPortrait?: Boolean,
  featuredSquare?: Boolean,
  renderSlots?: Function,
  showSample?: Boolean,
  onShowActivity?: Function,
  showFirstActivity?: Boolean,
  slideWidth?: number,
  renderSlides?: ()=>JSX.Element[],
}
const useStyles = makeStyles({
  root: (props: {wrap: Boolean}) => ({
    marginTop: '1rem',
    whiteSpace: props.wrap ? 'normal' : 'nowrap',
    // whiteSpace: 'normal',
    overflowX: 'scroll',
  }),
});

function SheduleRow({ renderSlides, slideWidth, showFirstActivity, onShowActivity, data, featuredPortrait, featuredSquare, renderSlots }: $props){  
  
  const { imageType, doNotHideAddedActivities, wrap } = useRowBlackbox();
  const classes = useStyles({wrap: !!(doNotHideAddedActivities || wrap)});
  const Slot = featuredPortrait ? PortraitSlot : featuredSquare ? SquareSlot : (imageType ? ImageSlot : SheduleSlot)
  slideWidth = slideWidth || (featuredPortrait ? 170 : featuredSquare ? 400 : 280);
  const slots = data.map((activity, i)=>{
    return <Slot showFirstActivity={!i && showFirstActivity} onShowActivity={onShowActivity} index={i} key={activity.id} shedule={activity} className="swiper-slide" />
  });
  if(renderSlots) return renderSlots(slots)
  if(wrap || doNotHideAddedActivities) return <div className={classes.root}>
    {
      slots
    }
  </div>
  return <Scroll slideWidth={slideWidth} renderSlides={renderSlides || (()=>slots)} />
  }

export default React.memo(SheduleRow)