import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { getMaterialId, materialTypeNotMaterial, materialTypeMatch } from '../utils';
import { createConfigAction, materialsSelector, materialTypesSelector } from '../../../utils/redux/config';
import { makeStyles } from '@material-ui/core';
import { $activity, $metaRequirement } from '../../../../declarations/local/activity';
import { $materialRowProps } from '..';

const useStyle = makeStyles({
  typeMatch: {
    color: '#e0e0e0 !important',
  }
})

const  Materials = React.memo(function Materials({render, activities} : {render: Function, activities: $activity[]}){
  const classes = useStyle();
  const materials = useSelector(materialsSelector);
  const materialTypes = useSelector(materialTypesSelector);
  const dispatch = useDispatch();
  const handleChange = (id: string, metaRequirement: $metaRequirement)=>(event: any)=>{
    const checked = event.target.checked || materialTypeNotMaterial(materials, materialTypes)(id, metaRequirement);
    const nextMaterials = {
      ...materials,
      [getMaterialId(id, metaRequirement)]: checked
    }
    const nextMaterialTypes = metaRequirement.types.reduce((acc: any, curr: string)=>{
      acc[curr] = checked;
      return acc
    }, {...materialTypes});
    dispatch(createConfigAction({
      materials: nextMaterials,
      materialTypes: nextMaterialTypes,
    }));
  }
  const prepareMetaRequirement = (activity: $activity, metaRequirement: $metaRequirement)=>{
    return {
      id: activity.id,
      checked: !!(materials[
        getMaterialId(activity.id, metaRequirement)
      ]) || 
      materialTypeMatch(materialTypes)(metaRequirement.types) || 
      false,
      checkedClass: materialTypeNotMaterial(materials, materialTypes)(activity.id, metaRequirement) && classes.typeMatch,
      onChange: handleChange(activity.id, metaRequirement),
      metaRequirement,
    }
  }
  const materialRowProps = activities.reduce((acc : $materialRowProps[], activity)=>{
    (activity?.properties?.metaRequirements || []).forEach((metaRequirement: $metaRequirement)=>{
      acc.push(prepareMetaRequirement(activity, metaRequirement));
    });
    return acc;
  }, [])
  return render(materialRowProps);
});

export default Materials;