import React from 'react';
import { useDispatch } from 'react-redux';
import { Check } from '@material-ui/icons';
import { Clear } from '@material-ui/icons';
import { ListItemText } from '@material-ui/core';
import { ListItemIcon } from '@material-ui/core';
import { MenuItem } from '@material-ui/core';


import IconMenu from '../../../../components/general/IconMenu';
import {
    addCompletedActivity,
    addDroppedActivity,
} from '../utils';

import { sendEvent } from '../../../../../utils/ga';

const AddActivityOptions = React.memo(function AddActivityOptions({activity, disabled}: {activity: any, disabled?: Boolean}){
  const dispatch = useDispatch();
  return <IconMenu
    renderMenuItems={(handleClose)=>{
      return [
    <MenuItem key="done" onClick={()=>{
      if(!disabled){
        sendEvent({category: 'scheduleOptions', action: 'click', label: 'finishedActivity'});
        dispatch(addCompletedActivity(activity))
      }
      handleClose()
    }}>
        <ListItemIcon>
          <Check color="primary" fontSize="small" />
        </ListItemIcon>
      <ListItemText primary="Done" />
      </MenuItem>,
        <MenuItem key="remove" onClick={()=>{
          if(!disabled){
            dispatch(addDroppedActivity(activity))
            sendEvent({category: 'scheduleOptions', action: 'click', label: 'dropActivity'});
          }
          handleClose() 
        }}><ListItemIcon>
            <Clear color="secondary" fontSize="small" />
            </ListItemIcon>
          <ListItemText primary="Remove" />
        </MenuItem>
      ]
    }}
  />
});

export default AddActivityOptions;