import React, { useContext } from 'react';

const ids = {};
const row = 0;

export type $rowBlackboxContext = {
  ids : {[key: string]: number},
  row: number,
  imageType?: 'landscape' | 'portrait'
  preview?: Boolean,
  doNotHideAddedActivities?: Boolean,
  wrap?: Boolean,
  hideSlotActions?: Boolean,
}

export const context : $rowBlackboxContext = {
  ids,
  row,
}

const rowBlackboxContext = React.createContext(context);

export const Provider = rowBlackboxContext.Provider;

export const useRowBlackbox = ()=>useContext(rowBlackboxContext);

