import { Box, Card, CardContent, Typography } from '@material-ui/core';
import React from 'react';
import classNames from 'classnames';
import { GridListTile } from '@material-ui/core';
import { GridList } from '@material-ui/core';
import { createStyles } from '@material-ui/styles';
import { makeStyles } from '@material-ui/styles';
import { Link } from 'react-router-dom';
import qs from 'query-string';
import { Rating } from '@material-ui/lab';
import { round } from 'lodash';
import MyTooltip from '../../../../components/general/MyTooltip';
export type $pack = {
  name: string,
  averageStarRating: number,
  images: {type: string, image: string, id: string}[]
}




interface $props {
  pack: $pack,
  className?: string,
  index?: number,
  onShowActivity?: Function,
  showFirstActivity?: Boolean,
}
const useStyles = makeStyles((theme: any)=>({
  root: {
    display: "inline-block",
    verticalAlign: 'top',
    marginRight: '1rem',
    marginBottom: '1rem',
    maxWidth: '400px',
  },
  title: {
    textDecoration: 'none',
    color: theme.palette.primary.main
  }
}));

const useGridStyles = makeStyles((theme: any) =>
  createStyles({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around',
      overflow: 'hidden',
      backgroundColor: theme.palette.background.paper,
    },
    gridList: {
      width: 400,
      maxWidth: "calc(100vh - 32px)",
      flexDirection: 'column',
      height: 500,
    },
  }),
);

type $image = {image: string, type: string};

function combinedGridTiles(images: $image[]){
  if(images.length >= 9) return images.slice(0, 9).map(({image}) => (
    <GridListTile key={image} cols={1}>
      <img src={image} alt={image} />
    </GridListTile>
  ))
  if(images.length >= 7) return images.map(({image}, index) => (
    <GridListTile key={image} rows={!index ? 3 : 1} cols={1}>
      <img src={image} alt={image} />
    </GridListTile>
  ));
  if(images.length >= 6) return images.map(({image}, index) => (
    <GridListTile key={image} rows={(!index || index === 3 || index === 4) ? 2 : 1}>
      <img src={image} alt={image} />
    </GridListTile>
  ));
  return images.slice(0, 4).map(({image}, index) => (
    <GridListTile key={image} rows={!index || index === (images.length - 1) ? 3 : 1} cols={!index ? 2 : 1}>
      <img src={image} alt={image} />
    </GridListTile>
  ));
 
}


const PacksCard = React.memo(function PacksCard(props: $props){
  const classes = useStyles();
  const gridClasses = useGridStyles();
  const linkTo = `/shedules/activities/packs?${qs.stringify({
    name: props.pack.name,
    ids: props.pack.images.map(image => image.id)
  })}`;
  
  const normalizedRating = props.pack.averageStarRating / 10;
  return <Card key={props.pack.name} className={classNames(props.className, classes.root)}>
    <CardContent>
      <Typography align="center" variant="body2"><Link to={linkTo}>{props.pack.name}</Link></Typography>
      <Box textAlign="center"><MyTooltip title={`The average rating for this activity is ${round(normalizedRating, 2)} out of 5`} render={({spreadProps})=><span {...spreadProps}><Rating value={normalizedRating} size="small" precision={0.1} readOnly /></span>}/></Box>
    </CardContent>
    <Link to={linkTo}><GridList cellHeight={160} className={gridClasses.gridList} cols={3}>
        {
          combinedGridTiles(props.pack.images)
        }
      </GridList>
    </Link>
  </Card>
})

export default PacksCard;



