import { Box, Paper } from '@material-ui/core';
import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { $activity } from '../../../../declarations/local/activity';
import { lineupActivitiesSelector, addedActivitiesSelector } from '../../../utils/redux/config';
import {
    removeActivityFromSchedule,
    addActivityToSchedule
} from '../../shedules/activities/added/utils';
import { makeStyles } from '@material-ui/styles';
import { Checkbox } from '@material-ui/core';
import { FormControlLabel } from '@material-ui/core';
import { AccordionDetails } from '@material-ui/core';
import { FormLabel } from '@material-ui/core';
import { ExpandMore } from '@material-ui/icons';
import { AccordionSummary } from '@material-ui/core';
import { Accordion } from '@material-ui/core';
import { $materialRowProps } from '..';
import MaterialRow from '../Row';
import Materials from '../Materials';
import { sortBy, uniqBy } from 'lodash';
import classNames from 'classnames';
import MyTooltip from '../../components/general/MyTooltip';

const useAccordionDetailsStyles = makeStyles({
  root: {
    display: 'block'
  }
})

const useStyles = makeStyles({
  avatar: {
    width: '15px',
    height: '15px',
    borderRadius: '8px',
    display: 'inline-block',
    verticalAlign: 'middle',
    marginRight: '2px',
  },
  legendFormLabel: {
    display: 'inline-block'
  },
})

const MasterList = React.memo(function MasterList(props: {data: $activity[]}){
  const accordionDetailsClasses = useAccordionDetailsStyles();
  const activitiesLineup = useSelector(lineupActivitiesSelector);
  const addedActivities =useSelector(addedActivitiesSelector);
  const [ orderedMaterials , setOrderedMaterials ] = useState<null|$materialRowProps[]>(null);
  const getOrderedMaterials = (materialRowsProps: $materialRowProps[])=>{
    if(orderedMaterials) return orderedMaterials;
    const nextOrderedMaterials = sortBy(materialRowsProps, (materialRowProps: $materialRowProps)=>materialRowProps.metaRequirement.types[0]);
    setOrderedMaterials(nextOrderedMaterials);
    return nextOrderedMaterials;
  }
  const dispatch = useDispatch();
  const classes = useStyles();
  const [ activities, setActivities ] = React.useState<{[key: string]: $activity}>({});
  React.useEffect(()=>{
    setActivities(props.data.reduce((acc: {[key: string]: $activity}, activity)=>{
      acc[activity.id] = activity;
      return acc;
    }, {}));
  }, [props.data]);
  return <Paper>
    <Accordion defaultExpanded>
        <AccordionSummary
          // classes={accordionClasses}
          expandIcon={<ExpandMore />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
        <FormLabel
          // className={classes.formLabel}
          component="legend">Master Supplies List</FormLabel>
        </AccordionSummary>
        <AccordionDetails classes={accordionDetailsClasses}>
          {
            props.data.filter(activity => addedActivities[activity.id]).map(activity => {
              const checked = activitiesLineup[activity.id];
              const onChange = ()=>dispatch(checked ? removeActivityFromSchedule(activity) : addActivityToSchedule(activity));
              return <div>
                <FormControlLabel
              classes={{root: classNames(classes.legendFormLabel, 'master-supplies-list__legend-item')}}
              control={
                <ColoredCheckbox
                  color={addedActivities[activity.id].color}
                  defaultChecked={checked}
                  onChange={onChange}
                  name={activity.id} />
                }
              label={activity.name}
            ></FormControlLabel>
            
            </div>
            })
          }
        </AccordionDetails>
      </Accordion>
      <Box p="16px">
        {
          Boolean(props.data.length) && <Materials
            activities={props.data}
            render={(materialRowsProps: $materialRowProps[])=>{
              const orderedMaterialRowsProps = getOrderedMaterials(materialRowsProps);
              const existingOrderedMaterialRowProps = orderedMaterialRowsProps.filter((materialRowProp)=>activitiesLineup[materialRowProp.id])
              const idsPerType = existingOrderedMaterialRowProps.reduce((acc: {[key: string]: string[]}, curr)=>{
                const type = curr.metaRequirement.types[0];
                if(!acc[type]){
                  acc[type] = []
                }
                acc[type].push(curr.id);
                return acc;
              }, {});
              return uniqBy(existingOrderedMaterialRowProps, (materialRowProps)=>materialRowProps.metaRequirement.types[0])
              .map((materialRowProp)=>{
                return <div key={materialRowProp.id + materialRowProp.metaRequirement.types[0]}>
                  <MaterialRow {...materialRowProp} />
                  {
                    idsPerType[materialRowProp.metaRequirement.types[0]].map((id)=><MyTooltip key={id} title={activities[id]?.name} render={({spreadProps})=><span {...spreadProps} className={classes.avatar} style={{backgroundColor: addedActivities[id].color}} />}/>)
                  }
                </div>;
              })
            }}
          />
        }
      </Box>
  </Paper>
});

export default MasterList;

const useCheckboxStyles = makeStyles({
  checked: ({color}: any)=>({
    '& svg': {
      color
    }
  })
})

const ColoredCheckbox = React.memo(({color, ...props} : any)=>{
  const classes = useCheckboxStyles({color});
  return <Checkbox
    {...props}
    classes={classes}
    />
});

