import React from 'react';
import { useLocation } from 'react-router-dom';
import { Provider } from '../../../../../utils/context/rowBlackbox';
import { SERVER_ADDRESS, useCustomSwr } from '../../../../../utils/remote';
import qs from 'query-string';
import { Button, Typography } from '@material-ui/core';
import SheduleRow from '../../../../components/general/SheduleRow';
import { addActivityToSchedule } from '../../added/utils';
import { useDispatch, useSelector } from 'react-redux';
import { userSelector } from '../../../../../utils/redux/user';
import MyPopover from '../../../../components/general/Popover';
import ReferPopover from '../../../../components/navigation/Top/Refer/Popover';

const defaultValue: any[] = [];

export default function PackCollection(){
  const location = useLocation();
  const dispatch = useDispatch();
  const user = useSelector(userSelector);
  const [showSuccessMessage, setShowSuccessMessage] = React.useState(false);
  const {ids, name} = qs.parse(location.search);
  const { data } = useCustomSwr(SERVER_ADDRESS+'/shedules/activities/public', {
    defaultValue,
    query: {
      ids,
    },
    deps: [],
  })
  const bulkDispatch = ()=>{
    if(!user) return;
    data.forEach((activity: any) => {
      dispatch(addActivityToSchedule(activity))
    });
    setShowSuccessMessage(true);
    setTimeout(()=>{
      setShowSuccessMessage(false);
    }, 2000)
  }
  const addButton = <Button color={showSuccessMessage ? "secondary" : "primary"} variant="contained" onClick={bulkDispatch}>{
    showSuccessMessage ? 'Success' : 'Add activities to bookmarks'
  }</Button>
  return <Provider value={{ids: {}, row: 0, wrap: true, imageType: 'portrait', hideSlotActions: true, doNotHideAddedActivities: true}}>
    <div>
      <Typography align="right" variant="h6" component="h1">
        {name}
      </Typography>
      {
        user ? addButton : <MyPopover renderHoverableContent={()=>addButton} renderOnHoverContent={()=><ReferPopover message={<Typography variant="body1">You can only add groups of activities if you are logged into your free account</Typography>} />} />
      }
      
      
      <SheduleRow data={data} />
    </div>
  </Provider>
}