import React from 'react';
import { Divider } from '@material-ui/core';

import { useCustomSwr, SERVER_ADDRESS } from '../../../../utils/remote';
import SheduleRow from '../../../components/general/SheduleRow';
import PacksCard, { $pack } from '../../../shedules/activities/Packs/Card';

const defaultValue : any[] = [];

const PackRow = React.memo(function PackRow({subjectTags, ages}: {subjectTags: string[], ages: string[]}){  
  const defaultTags =  ages;
  const tags = defaultTags.concat(subjectTags).map((t: string)=>'#'+t).reverse().join(' ');
  const { data } = useCustomSwr(SERVER_ADDRESS+`/shedules/activities/public/packs`, { 
    defaultValue,
    query: {
      tags,
    }, 
    deps: [],
    // cache: true
  });
  
  return !data.length && data !== defaultValue ? null : <div>
    {
      !data.length ? <p>No Results</p> : <SheduleRow renderSlides={()=>data.map((pack: $pack) => <PacksCard key={pack.name} className="swiper-slide" pack={pack} />)} data={data} />
    }
    <Divider variant="inset" />
  </div>
});

export default PackRow;